import React,{useEffect, useState} from 'react'
import { useSelector, useDispatch } from "react-redux";
import { Link, useHistory, useLocation,useParams} from 'react-router-dom';
import { AppDispatch } from "../../app/store";
import { fetchAsyncGetMyProf, selectLoginUserProfile } from '../auth/authSlice';
import { setBackUrl, setOpenProfile } from './homeSlice';
import styles from "./Home.module.css";
import { Avatar, Button,Drawer} from '@material-ui/core';
import img from './same.svg'
import Profile from './Profile';
import MenuIcon from '@material-ui/icons/Menu';
import PersonIcon from '@material-ui/icons/Person';
import HomeIcon from '@material-ui/icons/Home';
import GroupIcon from '@material-ui/icons/Group';
import AccountBoxIcon from '@material-ui/icons/AccountBox';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import CreateIcon from '@material-ui/icons/Create';

const Header:React.FC = () => {
    const dispatch: AppDispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();
    const loginUserProfile=useSelector(selectLoginUserProfile);
    const [isOpenMenu,setOpenMenu]=useState(false);
    

    useEffect(()=>{
        const fetchLoader = async ()=>{
            //ログインしていたら
            if (localStorage.localJWT) {
                await dispatch(fetchAsyncGetMyProf());//ログインしているユーザーのプロフィールを取得する
                //await dispatch(fetchAsyncGetBelongToGroup());
                if(location.pathname==='/'){
                    history.push('/home');
                }
            }else{
                if(!location.pathname.includes('/activate')){
                    dispatch(setBackUrl(location.pathname));
                    if(
                        !(
                            location.pathname.includes('/password_confirm') || 
                            location.pathname.includes('/contact') || 
                            location.pathname.includes('/disclaimer') || 
                            location.pathname.includes('/password/reset')
                        )
                    ){
                        history.push('/');
                    }
                }
            }
        };
        fetchLoader();
    },[dispatch,history,location.pathname]);

    return (
        <>
            {location.pathname!=='/'?
            <div className={styles.header_container}>
                <div className={styles.header_body}>
                    {/* <Link to='/home' className={styles.header_link}> */}
                        <div className={styles.header_body_left}>
                            {localStorage.localJWT ?
                            <>
                                
                                <div onClick={()=>setOpenMenu(true)}>
                                    <MenuIcon fontSize="large"/>
                                </div>
                                <Drawer
                                    anchor="left"
                                    open={isOpenMenu}
                                    onClose={() => {
                                        setOpenMenu(false)
                                    }}
                                >
                                    <div className={styles.navmune_profile_avater}>
                                        <Button onClick={()=>{dispatch(setOpenProfile())}}>
                                            <Avatar alt="who?" src={loginUserProfile.img} style={{height:'70px',width:'70px'}}/>
                                        </Button>
                                    </div>
                                    <div className={styles.navmune_profile_nickname}>
                                        <p className={styles.header_nickname}>{loginUserProfile.nickName}</p>  
                                    </div>
                                    <p className={styles.header_p_underbar}>_______________________________</p>
                                    <div className={styles.header_navbar_menu} onClick={()=>{dispatch(setOpenProfile());;setOpenMenu(false)}}>
                                        <PersonIcon/><h4 className={styles.header_navbar_menu_title}>プロフィール</h4>
                                    </div>
                                    <div className={styles.header_navbar_menu} onClick={()=>{history.push(`/home/`);;setOpenMenu(false)}}>
                                        <HomeIcon/><h4 className={styles.header_navbar_menu_title}>ホーム</h4>
                                    </div>
                                    {location.pathname.includes('/group') || location.pathname.includes('/game') || location.pathname.includes('/member') || location.pathname.includes('/matchrecord')?
                                    <>
                                        <div className={styles.header_navbar_menu} onClick={()=>{history.push(`/group/${location.pathname.split("/")[2]}`);window.scrollTo(0, 0);setOpenMenu(false)}}>
                                            <GroupIcon/><h4 className={styles.header_navbar_menu_title}>グループ</h4>
                                        </div>
                                        <div className={styles.header_navbar_menu} onClick={()=>{history.push(`/group/${location.pathname.split("/")[2]}/game`);window.scrollTo(0, 0);setOpenMenu(false)}}>
                                            <CreateIcon/><h4 className={styles.header_navbar_menu_title}>対局</h4>
                                        </div>
                                        <div className={styles.header_navbar_menu} onClick={()=>{history.push(`/group/${location.pathname.split("/")[2]}/member`);window.scrollTo(0, 0);setOpenMenu(false)}}>
                                            <AccountBoxIcon/><h4 className={styles.header_navbar_menu_title}>メンバー</h4>
                                        </div>
                                        <div className={styles.header_navbar_menu}onClick={()=>{history.push(`/group/${location.pathname.split("/")[2]}/matchrecord`);window.scrollTo(0, 0);setOpenMenu(false)}}>
                                            <MenuBookIcon/><h4 className={styles.header_navbar_menu_title}>記録</h4>
                                        </div>
                                    </>
                                    :null}
                                </Drawer>
                             </>
                            :
                             <Link to='/home' className={styles.header_link}> 
                                <img src={img} className={styles.header_img} width="70px" height="70px" alt="header_img"/>
                             </Link> 
                                }
                        </div>
                    
                    {location.pathname.includes('/activate') || location.pathname.includes('/password') || location.pathname.includes('/contact') || location.pathname.includes('/disclaimer')?
                    <div></div>
                    :
                        <div className={styles.header_body_right}>
                            <Profile />
                        </div>
                    }
                </div>
            </div>
            :null}
        </>
    )
}

export default Header
